import React, { createContext, useEffect, useState } from 'react';
import { Provider } from 'react-redux';
import Store from "./store";
import { ReactLocation, Router, createHashHistory } from "@tanstack/react-location";
import { ChatPage, HomePage, LoginPage, RegisterPage } from './pages';
import Layout from './pages/layout/Layout';
import { Notifications } from '@mantine/notifications';
import ConfigContext from './components/theme'
import './themes/pt-br_en.css'
import './App.scss'


const App = () => {
  const location = new ReactLocation();
  
  return (
    <Provider store={Store}>
      <ConfigContext.Provider value={{
          language: 'pt-br',
          primaryColor: '#3A9B39',
          secondColor: '#172776',
          tertiaryColor: '#EDC90A',
        }}>
        <Router
          location={location}
          routes={[
            { path: "/", element: <HomePage /> },
            { path: "/login", element: <LoginPage /> },
            { path: "/register", element: <RegisterPage /> },
            { path: "/chat", element: <ChatPage /> },
          ]}>
          <Layout />
          <Notifications />
        </Router>
      </ConfigContext.Provider>
    </Provider>
  );
}
export default App