import React from "react";

interface IThemeContext {
  language: 'pt-br' | 'en',
  primaryColor: string,
  secondColor: string,
  tertiaryColor: string,
}
const ConfigContext = React.createContext<IThemeContext>({
  language: 'pt-br',
  primaryColor: '#3A9B39',
  secondColor: '#172776',
  tertiaryColor: '#EDC90A',
});
export default ConfigContext;